<template>
    <div class='background'>
        <header-nav class="istutorial"></header-nav>
   
        <div style='width:100%;display:inline-block;background:#BBF597'>
            <div style='padding:0 1em 0 1em;margin:0 0 0 0;float:left;display:inline-block;font-size:3em;text-align:left;'>
                The tutorial has four parts: 
            </div>
            <div style='padding:0 1em 0 2.5em;margin:0 0 0 0;float:left;display:inline-block;font-size:2.3em;text-align:left;'>
                1) Query, 2) Result Interpretation, 3) Download, 4) Extension.
            </div>
        </div>
        <div style='padding:2em 0 2em 0;margin:0 0 0 0;width:100%;float:left;display:inline-block;'> 
            <div style='padding:0 0 0 0;margin:0 0 0 0;width:90%;padding-left:5%;padding-right:5%;height:210em;background:#BBF597'> 
                <div>
                    <div class='function_name'>
                        1) Query
                    </div>
                    <div class='function_content'>
                        The database provides two methods of searching, fast searching and advanced search. Opening the database, 
you can see the advanced search, clicking on species, sources, and treatments, the results will pop up.
                    </div>
                    <img v-bind:src="pic1"/>
                    <div class='function_content'>
                        The database also provides a fast searching, you can click Fast Cell-line Search button, the search
window pops up. Please input the cell line that you are interested in, the server will get the result quickly.                         
                    </div>
                    <img v-bind:src="pic2"/>
                    <div class='function_content'>
                        You need to click a sample from multiple repulicates. And the result will pop up.                         
                    </div>                        
                    <img v-bind:src="pic3"/>
                    <div class='function_name'>
                        2) Result Interpretation
                    </div>
                    <div class='function_content'>
                        After locating the specific sample, you can get a summary information table of the sample, including
GSM ID, citation, tissue etc. You can click the Get detailed result button to find a visulization table with more information
including qc ,expression level, nascent RNA, enhancer RNA, and use the navigation bar to switch tables. 
                    </div>
                    <img v-bind:src="pic4"/>
                    <img v-bind:src="pic5"/>
                    <div class='function_name'>
                        3) Download
                    </div>
                    <div class='function_content'>
                        Each sample has 5 different files to download. You can locate a sample and click the download button to get the
results. We also provide batch download function on download page.
                    </div>   
                    <div class='function_name'>
                        4) Extension
                    </div>
                    <div class='function_content'>
                         After getting the 5 result files, you can do further research. For example, you can find out
the transcriptional feature of the genes you are interested under certain cell conditions with bw file download from this
database and genome browser tool like IGV.
                    </div>  
                    <img v-bind:src="pic6"/>
                    <div class='function_content'>
                        You can do differential expression analysis combined with TPM value in nascent RNA expression
level file.
                    </div>
                    <div class='function_content'>
                        You can find out the motif in enhancer RNA from different cells.
                    </div>                    
                </div>
            </div>
        </div>
        <div style='width:100%;display:inline-block;'>        
        </div>        
        <Footer></Footer>
        
    </div>
</template>    
<script>
    import HeaderNav from '../components/headernav.vue'
    import Footer from '../components/footer.vue'
    export default{
        components: {
            HeaderNav,
            Footer,    
        },
        data(){
            return{
                pic1:'/static/tutorial1.png',
                pic2:'/static/tutorial2.png',
                pic3:'/static/tutorial3.png',
                pic4:'/static/tutorial4.png',
                pic5:'/static/tutorial5.png',
                pic6:'/static/tutorial6.png'
               
                
            }
        }        
    }    
</script>
<style scoped>
    .background{
        background:url('../assets/background.jpg');
    }
    .function_name{
        font-size:3em;
        padding:0 0 0 0;
        margin:0 0 0 0;
        float:left;
        width:100%;
        text-align:left;        
    }
    .function_content{
        font-size:2em;
        padding:0 0 0 0;
        margin:0 0 0 0;
        float:left;
        width:100%;
        text-align:left;       
    }
</style> 


