                    <div class='user_question'>
                        1. Does the database provide downloads of the original sequencing file, such as sra/bam/bed format?
                    </div> 
                    <div class='author_answer'>
                        A: In this database, we provide five sets of data for users to download of each sample, BW file, Qc file, 
    Expressed level for transcripts, Nascent RNA, and Enhancer RNA.This database does not provide downloads of the original file, 
    but if there is a need, you can click on the GSM sample id to jump to the data source website for download.
                    </div>  
                    <div class='user_question'>
                        2. Which version of the reference genome is used for reads mapping?
                    </div> 
                    <div class='author_answer'>
                        A: We choose 2018 latest genome and reference annotations from Ensembl. You can get the data by the
links here.</br>
( Genome:
