<a href='ftp://ftp.ensembl.org/pub/release-92/fasta/caenorhabditis_elegans/dna/Caenorhabditis_elegans.WBcel235.dna.toplevel.fa.gz' target="_blank">Caenorhabditis_elegans.WBcel235.dna.toplevel.fa.gz</a>,&nbsp&nbsp
<a href='ftp://ftp.ensembl.org/pub/release-92/fasta/drosophila_melanogaster/dna/Drosophila_melanogaster.BDGP6.dna.toplevel.fa.gz' target="_blank">Drosophila_melanogaster.BDGP6.dna.toplevel.fa.gz</a>,&nbsp&nbsp
<a href='ftp://ftp.ensembl.org/pub/release-92/fasta/homo_sapiens/dna/Homo_sapiens.GRCh38.dna.toplevel.fa.gz' target="_blank">Homo_sapiens.GRCh38.dna.toplevel.fa.gz</a>,&nbsp&nbsp
<a href='ftp://ftp.ensembl.org/pub/release-92/fasta/mus_musculus/dna/Mus_musculus.GRCm38.dna.toplevel.fa.gz' target="_blank">Mus_musculus.GRCm38.dna.toplevel.fa.gz</a>,&nbsp&nbsp
<a href='ftp://ftp.ensemblgenomes.org/pub/release-38/protists/fasta/plasmodium_falciparum/dna/Plasmodium_falciparum.EPr1.dna.toplevel.fa.gz' target="_blank">Plasmodium_falciparum.EPr1.dna.toplevel.fa.gz</a>,&nbsp&nbsp
