<a href='ftp://ftp.ensembl.org/pub/release-92/gtf/drosophila_melanogaster/Drosophila_melanogaster.BDGP6.92.gtf.gz' target="_blank">Drosophila_melanogaster.BDGP6.92.gtf.gz</a>,&nbsp&nbsp
<a href='ftp://ftp.ensembl.org/pub/release-92/gtf/homo_sapiens/Homo_sapiens.GRCh38.92.gtf.gz' target="_blank">Homo_sapiens.GRCh38.92.gtf.gz</a>,&nbsp&nbsp
<a href='ftp://ftp.ensembl.org/pub/release-92/gtf/mus_musculus/Mus_musculus.GRCm38.92.gtf.gz' target="_blank">Mus_musculus.GRCm38.92.gtf.gz</a>,&nbsp&nbsp
<a href='ftp://ftp.ensemblgenomes.org/pub/release-38/protists/gtf/plasmodium_falciparum/Plasmodium_falciparum.EPr1.38.gtf.gz' target="_blank">Plasmodium_falciparum.EPr1.38.gtf.gz</a>,&nbsp&nbsp
<a href='ftp://ftp.ensembl.org/pub/release-92/gtf/rattus_norvegicus/Rattus_norvegicus.Rnor_6.0.92.gtf.gz' target="_blank">Rattus_norvegicus.Rnor_6.0.92.gtf.gz</a>,&nbsp&nbsp
