<a href='ftp://ftp.ensemblgenomes.org/pub/release-38/plants/gtf/zea_mays/Zea_mays.AGPv4.38.gtf.gz' target="_blank">Zea_mays.AGPv4.38.gtf.gz</a>&nbsp&nbsp

) 
                    </div>                     
                    <div class='user_question'>
                        3. Why does TMP value be used for gene expression instead of FPKM/RPKM?
                    </div> 
                    <div class='author_answer'>
                        A: TPM is often used to compare different tissues in the same species. Since the TPM value of all genes
    in each sample is added after normalization, the sum is unique. Therefore, the expression of the same gene in different 
    tissues can be compared directly. According to the features of GRO-seq sequencing, in the advanced search the way we provide
    are different species → different cell lines → different treatments to facilitate users to do parallel comparison among
    the same species.
                    </div> 
                    <div class='user_question'>
                        4. What is the identification criteria for enhancer RNA?
                    </div> 
                    <div class='author_answer'>
                        A: In the existing literature, the enhancer RNA was identified in the intergenic region. The portion of 
    the transcript that overlaps with the gene region is discarded. We searched for regions mare than 10kb away from the 5' or 3'
    end of the annotation gene. Nascent RNA transcripts less than 9 kb were defined as enhancer RNA. Besides, users can download
    the nascent RNA file and set the standard for identification according to the specific needs.
                    </div> 
                </div>
