<template>
    <div id='result_detailed_div'>
        <ul>
            <li v-for="(item,index) in tabs" :class="{active:index == num}" @click="tab(index)">
                {{item}}
            </li>
        </ul>
        <div class="tabCon">            
            <div v-for='(itemCon,index) in tabs' v-show="index == num">
            </div>
            <div >
                <div v-show="judge_item(0)">                    
                    <div class='row_class'>
                        <div class='row_one'>
                            <div class='row_one_label'>Trim drop rate:</div>
                            <div class='row_one_content'>{{ gsm_detail.qc_trim_rate}}</div>
                        </div>   
                        <div class='row_one'>                    
                            <div class='row_one_label'>Mapping rate:</div>
                            <div class='row_one_content'>{{ gsm_detail.qc_mapping_rate}}</div>
                        </div> 
                        <div class='row_one'>                        
                            <div class='row_one_label'>Unique map rate:</div>
                            <div class='row_one_content'>{{ gsm_detail.qc_unique_mapping_rate}}</div>
                        </div>
                    </div> 
                    <div class='row_class'>
                        <div class='row_two'>
                            <div class='row_one_label'>Sequence length:</div>
                            <div class='row_one_content'>{{ gsm_detail.qc_sequence_length}} bp</div>
                        </div>
                        <div class='row_two'>
                            <div class='row_one_label'>%GC:</div>
                            <div class='row_one_content'>{{ gsm_detail.qc_gc_rate}}</div>
                        </div>                       
                    </div>
                    <div class='row_class'>
                        <div>
                            <button @click="download_qc(gsm_detail.gsm_id)" style='width:25%;height:3em;padding:0 0 0 0;margin:0 0 0 0;float:left;'>Qc file download ({{gsm_detail.qc_file_size}})</button>
                        </div>                       
                    </div>                    
                </div>
                <div v-show="judge_item(1)" style='width:100%;height:15.8em;padding:0;margin;0'>  
                    <div id='tpm_bar_id' style='width:50%;height:15em;padding:0 0 0 0;margin:0 0 0 0;float:left;'></div>
                    
                    <div id='tpm_pie_id' style='width:50%;height:12.5em;padding:0 0 0 0;margin:0 0 0 0;float:left;'></div>
                    <button @click="download_tpm(gsm_detail.gsm_id)"style='width:25%;height:3em;padding:0 0 0 0;margin:0 0 0 0;float:right;margin-right:10%;'>TPM file download ({{gsm_detail.tpm_file_size}})</button>
                    
                </div>
                <div v-show="judge_item(2)" >
                    <div style='width:50%;float:left'>
                        <div style='margin-top:1.5em;width:100%;display:inline-block;'>
                            <div class='row_one_label' style='margin-left:15%'>Intergenic Nascent RNA Number:</div>
                            <div class='row_one_content'>{{ gsm_detail.nascent_inter}}</div>
                        </div>
                        <div style='margin-top:2em;width:100%;display:inline-block;'>
                            <div class='row_one_label' style='margin-left:15%'>Intragenic Nascent RNA Number:</div>
                            <div class='row_one_content'>{{ gsm_detail.nascent_intra}}</div>
                        </div> 
                        <div class='row_class' style='margin-top:2em;margin-left:20%'>                        
                            <button @click="download_nascent(gsm_detail.gsm_id)" style='width:40%;height:3em;padding:0 0 0 0;margin:0 0 0 0;float:left;'>Nascent Transcript file download ({{gsm_detail.nascent_file_size}})</button>
                        </div>
                    </div>                            
                    <div id='nascent_pie_id' style='width:50%;height:15.5em;padding:0;margin;0;float:left'></div>  
                </div>
                <div v-show="judge_item(3)" >
                    <div style='width:50%;float:left'>                
                        <div style='margin-top:1.5em;width:100%;display:inline-block;'>
                            <div class='row_one_label' style='margin-left:15%'>The Average Length Of Enhancer RNA:</div>
                            <div class='row_one_content'>{{ (gsm_detail.enhancer_length_total/gsm_detail.enhancer_number).toFixed(2) }}</div>
                        </div>                
                        <div style='margin-top:2em;width:100%;display:inline-block;'>
                            <div class='row_one_label' style='margin-left:15%'>Enhancer RNA Number:</div>
                            <div class='row_one_content'>{{ gsm_detail.enhancer_number}}</div>
                        </div> 
                        <div class='row_class' style='margin-top:2em;margin-left:20%'>                        
                            <button @click="download_enhancer(gsm_detail.gsm_id)" style='width:40%;height:3em;padding:0 0 0 0;margin:0 0 0 0;float:left;'>Enhancer RNA file download ({{gsm_detail.enhancer_file_size}})</button>
                        </div>
                    </div>    
                    <div id='enhancer_pie_id' style='width:50%;height:15.5em;padding:0;margin;0;float:left'></div>  
                </div>                
            </div>                
        </div>
    </div>      
</template>

<script>
    import echarts from 'echarts'
    import reqwest from 'reqwest' 
    export default {
        data(){
            return{
                gsm_detail:[],
                tabs: ["Quality Control", "Gene Expression Level","Nascent Transcript","Enhancer RNA"],
                num: 0,
                expression_distribution:[
                    {name:'a_distribution',value:30},
                    {name:'b_distribution',value:35},
                    {name:'c_distribution',value:10},
                    {name:'d_distribution',value:25}
                ]         
            }
        },
        methods: {
            tab(index) {
                this.num = index;
            }, 
            draw_pie(id,input_data,title_data,subtitle_data,data_title){
                var width_canvas = parseInt(0.47*window.innerWidth);                
                var height_canvas = 'auto';
                this.charts = echarts.init(document.getElementById(id),null,{width:width_canvas,height:height_canvas});
                this.charts.setOption({
                    title:{
                        text:title_data,
                        subtext:subtitle_data,
                        left:'center',
                    },
                    tooltip: {
                       trigger: 'item',
                       formatter: '{a}<br/>{b}:{c} ({d}%)'
                    },
                    series: [
                        {
                            name:data_title,
                            type:'pie',
                            center:['60%','65%'],
                            radius:['25%','48%'],
                            data:input_data
                        }

                    ]                  
                })                
            },  
            draw_bar(id, top_5_name,top_5_array){
                var width_canvas = parseInt(0.47*window.innerWidth);                
                var height_canvas = 'auto';
                this.charts = echarts.init(document.getElementById(id),null,{width:width_canvas,height:height_canvas});
                this.charts.setOption({
                    title:{
                        text:'Top 5 expressed transcripts',
                        left:'center'
                    },
                    grid:{
                        left:'20%'
                    },                
                    xAxis: {
                        type: 'value',
                        name: 'TPM',
                        nameTextStyle:{
                            fontWeight:'bold'
                        },
                        axisLabel: {
                            formatter: '{value}'
                        }
                    },
                    yAxis: {
                        type: 'category',
                        name:'Transcript',
                        nameTextStyle:{
                            fontWeight:'bold'
                        },
                        data: top_5_name, 
                        axisLabel:{
                            rotate:35
                        }
                    },
                    series:[
                        {
                            name: 'transcripts tpm value',
                            type: 'bar',
                            data: top_5_array
                        }
                    ]
                })                
            },             
            judge_item:function(item){
                if(item==this.num){
                    return true
                }
                else{
                    return false
                }        
            },
            drop_0_item_in_array(input_array){
                var new_array=[];
                for(var index in input_array){
                    var vals = Object.values(input_array[index])
                    if(vals[0] !=0){
                        new_array.push(input_array[index]);
                    };
                };
                return new_array;
            },
            change_dict_to_name_value(input_array){
                var new_array=[];
                for(var index in input_array){                
                    var keys = Object.keys(input_array[index])
                    var vals = Object.values(input_array[index])
                    var new_dic={'name':keys,'value':vals}
                    new_array.push(new_dic)
                }
                return new_array;
            },
            download_qc(input_gsm_id){
                window.open('http://mcube.nju.edu.cn/grobase/api/download/qc/'+input_gsm_id+'_fastqc.html')
            },
            download_bw(input_gsm_id){
                window.open('http://mcube.nju.edu.cn/grobase/api/download/bw/'+input_gsm_id+'.bw')
            },
            download_tpm(input_gsm_id){
                window.open('http://mcube.nju.edu.cn/grobase/api/download/expression_tpm/'+input_gsm_id+'_expression.txt')
            },            
            download_nascent(input_gsm_id){
                window.open('http://mcube.nju.edu.cn/grobase/api/download/nascent_curation/'+input_gsm_id+'_nascent.txt')
            },            
            download_enhancer(input_gsm_id){
                window.open('http://mcube.nju.edu.cn/grobase/api/download/enhancer_curation/'+input_gsm_id+'_enhancer.bed')
            }
        },          
        props:['all_data'],
        mounted(){
            var self=this;                 
            self.$nextTick(function() {
                self.draw_bar('tpm_bar_id');                
            })
        },        
        created(){
            var self=this;
            reqwest({
                url:'http://mcube.nju.edu.cn/grobase/api/gsm_detail_json/'+self.all_data+'?format=json',
                type:'json',
                method:'get',
                success:function(response_data){
                    self.gsm_detail = response_data;
                    var tpm_pie_data=[
                        {'0':self.gsm_detail['tpm_0']},
                        {'0-1':self.gsm_detail['tpm_0_1']},
                        {'1-10':self.gsm_detail['tpm_1_10']},
                        {'10-100':self.gsm_detail['tpm_10_100']},
                        {'100-1000':self.gsm_detail['tpm_100_1000']},
                        {'1000-10000':self.gsm_detail['tpm_1000_10000']},
                        {'10000-100000':self.gsm_detail['tpm_10000_100000']},
                        {'over 100000':self.gsm_detail['tpm_above_100000']}
                    ];  
                    var drop_0_tpm_pie_data=self.drop_0_item_in_array(tpm_pie_data);
                    var name_value_tpm_pie_data=self.change_dict_to_name_value(drop_0_tpm_pie_data);
                    self.draw_pie('tpm_pie_id',name_value_tpm_pie_data,'Expression Level (TPM) Distribution','','Expressed Transcripts Number');  
                    var nascent_pie_data=[
                        {'below 100':self.gsm_detail['nascent_below_100']},
                        {'100-1000':self.gsm_detail['nascent_100_1000']},
                        {'1000-5000':self.gsm_detail['nascent_1000_5000']},
                        {'5000-10000':self.gsm_detail['nascent_5000_10000']},
                        {'10000-50000':self.gsm_detail['nascent_10000_50000']},
                        {'50000-100000':self.gsm_detail['nascent_50000_100000']},
                        {'over 100000':self.gsm_detail['nascent_over_100000']}                        
                    ];
                    var drop_0_nascent_pie_data=self.drop_0_item_in_array(nascent_pie_data);
                    var name_value_nascent_pie_data=self.change_dict_to_name_value(drop_0_nascent_pie_data);
                    var total_nascent_number = self.gsm_detail['nascent_inter']+self.gsm_detail['nascent_intra']
                    self.draw_pie('nascent_pie_id',name_value_nascent_pie_data,'Nascent RNA Length Distribution','Total Nascent Number: '+total_nascent_number,'Nascent RNA Number');  
                    var enhancer_pie_data=[
                        {'below 100':self.gsm_detail['enhancer_below_100']},
                        {'100-500':self.gsm_detail['nascent_100_500']},
                        {'500-1000':self.gsm_detail['nascent_500_1000']},
                        {'1000-5000':self.gsm_detail['nascent_1000_5000']},
                        {'5000-9000':self.gsm_detail['nascent_5000_9000']}                     
                    ];
                    var drop_0_enhancer_pie_data=self.drop_0_item_in_array(enhancer_pie_data);
                    var name_value_enhancer_pie_data=self.change_dict_to_name_value(drop_0_enhancer_pie_data);
                    self.draw_pie('enhancer_pie_id',name_value_enhancer_pie_data,'Enhancer RNA Length Distribution','','Enhancer RNA Number');  
                    var tpm_line = self.gsm_detail['tpm_top_5'];
                    var tpm_array = tpm_line.slice(2,-2).split("##")
                    var tpm_transcript_name_array = []
                    var tpm_top_5_data=[]
                    for(var tpm_i=0; tpm_i<5;tpm_i =tpm_i+1){
                        var tpm_transcript_name = tpm_array[tpm_i].split("#")[0];
                        var transcript_tpm_value = parseFloat(tpm_array[tpm_i].split("#")[1]);
                        tpm_transcript_name_array.push(tpm_transcript_name);
                        tpm_top_5_data.push(transcript_tpm_value);
                    }
                    self.draw_bar('tpm_bar_id',tpm_transcript_name_array,tpm_top_5_data);                    
                }
            });            
        }
    }            
</script>

<style scoped>
    #result_detailed_div{
        margin:0;
        padding:0;    
        margin-left:3%;
        margin-right:3%;    
        border:0.9em solid #ADD1D6;
        height:20em;
        background-color:#BBF597;
        margin:1.5em;       
    }
    ul{
        padding:0;
        margin:0;
        width:100%;
        height:4.2em;        
      
    }
    li{        
        float:left;
        list-style:none;
        width:25%;
        height:2em;
        font-size:1.5em;
        padding-top:1em;
        text-shadow:blue 0 0 1em;
        color:white;  
        
    }  
    li:hover{
        background-color:#FED966;
    }     
    .active{
        background-color:#FED966;
    }
    .tabCon{
        width:100%;
        float:left;
        height:15.8em;
    }
    .row_class{
        width:100%;
        padding-top:1em;
        padding-bottom:2em;
        margin-top:1em;
        margin-left:3%;
        margin-right:3%;   
    }
    .row_one{
        float:left;
        width:33%;    
    }
    .row_one_label{
        text-shadow:blue 0 0 1em;
        color:white;
        font-size:1.5em;  
        float:left;    
    }
    .row_one_content{
        font-size:1.5em;  
        float:left; 
        margin-left:0.6em;
    }
    .row_two{
        float:left;
        width:50%;
    }  
    button:hover{
        background-color:#EA493F
    }
    
</style>


