<template>
    <div>
        <header-nav class="isabout"></header-nav>
        <div style='width:100%;display:inline-block;'></div>          
        <div id='about_div'>
            <div class='title_div'>What is GROBASE?</div>
            <p class='text_p'>&nbsp&nbsp&nbsp&nbspGrobase is a repository of 103777571 nascent RNA transcripts with the average
length 20375bp and 18279934 enhancer RNA with the average length 374bp from different sources of GRO-Seq data. The original 
sequencing file was curated artificially. With a uniform pre-process flow, we computed expression level of annotated genes, 
called nascent transcripts, and further identified enhancer RNA.</br>           
            &nbsp&nbsp&nbsp&nbspThe database provides a user friendly query method. We provide more than
20Gb results file to download, which cover 9 species, cell lines and 620 samples. Besides, the database also provides data 
visualization system for each sample, which facilitates users to further explore data.</br>            
            </p>               
            <div class='title_div'>Data Processing Pipeline</div>
            <p class='text_p'>
                /sratoolkit.2.8.2-1-centos_linux64/bin/fastq-dump --split-3 sra_file -O original_fastq_file</br></br>
                /FastQC-v0.11.5/fastqc original_fastq_file -o qc_file</br></br>
                java -jar /Trimmomatic-0.36/trimmomatic-0.36.jar SE -threads 30 original_fastq_file trim.fastq_file ILLUMINACLIP:TruSeq3-SE.fa:2:30:10 LEADING:20 TRAILING:20 SLIDINGWINDOW:4:20 MINLEN:32 2</br></br>
                /bwa-0.7.15/bwa mem ../bwaindex/bwa_index -t 30 trim_fastq_file > align_result.sam</br></br>
                /RSeQC-2.6.4/bam_stat.py -i align_result.sam</br></br>
                /samtools-1.7/samtools view -bS --threads 30 align_result.sam >align_result.bam</br></br>
                /samtools-1.7/samtools rmdup -s align_result.bam align_result_rmdup.bam</br></br>
                /samtools-1.7/samtools sort -m 10G --threads 30 align_result_rmdup.bam -o sorted_result.bam</br></br>
                /samtools-1.7/samtools merge GSM_result.bam sorted_result1.bam sorted_result2.bam</br></br>
                /samtools-1.7/samtools index GSM_result.bam GSM_result.bam.bai</br></br>
                /deepTools-3.0.1/bin/bamCoverage -b GSM_result.bam -o GSM_result.bw</br></br>
                /subread-1.6.0-Linux-x86_64/bin/featureCounts -a Homo_sapiens.GRCh38.91.gtf -T 10 -o GSM.annotation GSM_result.bam</br></br>
                /R (3.4)/groHMM1.12/detectTranscripts(reads_grange, LtProbB=-200, UTS=5,threshold=1)</br></br>
            </p>
        </div>
        <div style='width:100%;display:inline-block;'></div>  
        <Footer></Footer>
    </div>
</template>

<style scoped>
    
    #about_div{
        margin-top:-2em;
        padding:0;
        background:#BBF597;       
    }
    .title_div{
        text-align:left;
        margin-left:1em;
        font-size:3em;
    }
    .text_p{
        margin-left:2em;
        margin-right:2em;
        font-size:1.5em;
        text-align:left;
        
    }
</style>
<script>
    import HeaderNav from '../components/headernav.vue'
    import Footer from '../components/footer.vue'

    export default{
        components: {
            HeaderNav,
            Footer,

        },
        data(){
            return{
                isNowPage:true
            }
        }
    }
</script>


