{name: 'GSM1183915',value: [51375.89648733,-40460.08655579,-17450.37845532],itemStyle:{color:'#c78419'}},
{name: 'GSM1893950',value: [417718.50683857,64022.02693078,29015.59910083],itemStyle:{color:'#4f19c7'}}               
                ]
            }
        },
        methods:{
            drawPie(id){
                this.charts = echarts.init(document.getElementById(id))
                this.charts.setOption({
                    tooltip: {
                       trigger: 'item',
                       formatter: '{a}<br/>{b}:{c} ({d}%)'
                    },
                    legend: {
                        orient: 'vertical',
                        x: 'left',
                        data:this.opinion
                    },
                    series: [
                       {
                            name:'Number of samples',
                            type:'pie',
                            radius:['50%','70%'],
                            avoidLabelOverlap: false,
                            label: {
                                normal: {
                                    show: false,
                                    position: 'center'
                                },
                                emphasis: {
                                    show: true,
                                    textStyle: {
                                    fontSize: '30',
                                    fontWeight: 'blod'
                                    }
                                }
                            },
                            labelLine: {
                                normal: {
                                    show: false
                                }
                            },
                            data:this.opinionData
                        }
                    ]
                })
            },
            draw_3d_scatter(id,pca_data){
                this.charts = echarts.init(document.getElementById(id));
                this.charts.setOption({
                    grid3D: {},
                    xAxis3D: {},
                    yAxis3D: {},
                    zAxis3D: {},                
                    tooltip:{},
                  
                    series: [{
                        coordinateSystem:'cartesian3D',
                        name:'Three Principal Components',
                        type: 'scatter3D',
                        data: pca_data

                    }]  
                })
            },
        },
      //调用
        mounted(){
            this.$nextTick(function() {
                this.drawPie('specie_samples_num_pic');
                this.draw_3d_scatter('human_tissue_pca_scatter',this.human_pca_data);
                this.draw_3d_scatter('mus_tissue_pca_scatter',this.mus_pca_data);
            })           
        }        
    }
</script>        


