<template>
    <div id='result_summary'>
        <div class='row_class'>
            <div class='row_one'>
                <div class='row_one_label'>GSM:</div>
                <div class='row_one_content'>
                    <a v-bind:href="'https://www.ncbi.nlm.nih.gov/geo/query/acc.cgi?acc='+all_data.gsm" target="_blank">{{ all_data.gsm }}</a>
                </div>
            </div>    
            <div class='row_one'>
                <div class='row_one_label'>PMID:</div>
                <div class='row_one_content' v-for="pmid_term in this.all_data.pmid.toString().split('#')">
                    <a v-bind:href="'https://www.ncbi.nlm.nih.gov/pubmed/?term='+pmid_term" target="_blank">{{ pmid_term }}</a>                
                </div>
            </div>
            <div class='row_one'>
                <div class='row_one_label'>Submit Time:</div>
                <div class='row_one_content'>{{ all_data.submit_date }}</div>
            </div>    
        </div>
        <div class='row_class'>
            <div id='citation_label'>Citation:</div>
            <div id='citation_content'>{{ all_data.citation }}</div>
        </div>
        <div class='row_class'>
            <div class='row_three_left'>
                <div class='row_three_label'>
                    Tissue:
                </div>
                <div class='row_three_content'>
                    {{ all_data.tissue }}
                </div> 
            </div>
            <div class='row_three_right'>
                <div class='row_three_label'>
                    Organization:
                </div>
                <div class='row_three_content'>
                    {{ all_data.oganization}}
                </div>
            </div>
        </div>
        <!--<div class='row_class'>
            <div id='study_id'>
                <div id='study_id_label'>
                    Study ID:
                </div>
                <div id='study_id_content'>
                </div>       
            </div>    
        </div>-->
        <div class='row_class'>
            <div class='row_one'>
                <div class='row_one_label'>
                    Bigwig file:
                </div>
                <div class='row_one_content'>                
                    <a :href="'http://mcube.nju.edu.cn/grobase/api/download/bw/'+all_data.gsm+'.bw'" target="_blank">{{ all_data.gsm }}.bw</a>
                </div>
            </div>     
            <div class='row_one'>
                <div class='row_one_label'>
                    Bigwig file size: 
                </div>
                <div class='row_one_content'> 
                    {{all_data.bwsize}}
                </div>
            </div>       
            <div class='row_one'>
                <button v-on:click='change_detail_button()' style='width:15em;height:4em; font-size:1.4em'>Get detailed result</button>
            </div>
        </div>
        
    </div>
</template>

<style scoped>
    #result_summary{
        margin:0;
        padding:0;    
        margin-left:3%;
        margin-right:3%;    
        border:0.9em solid #ADD1D6;
        height:20em;
        background-color:#BBF597;
        margin:2em;
    }
    .row_class{
        width:100%;
        padding-top:1em;
        padding-bottom:2em;
        margin-top:1em;
        margin-left:3%;
        margin-right:3%;   
    }
    .row_one{
        float:left;
        width:33%;    
    }
    .row_one_label{
        text-shadow:blue 0 0 1em;
        color:white;
        font-size:1.5em;  
        float:left;    
    }
    .row_one_content{
        font-size:1.5em;  
        float:left; 
        margin-left:0.6em;
    }    
    #citation_label{
        text-shadow:blue 0 0 1em;
        color:white;
        font-size:1.5em; 
        float:left; 
        text-align:left;
    }

    #citation_content{
        font-size:1.5em;  
        float:left; 
        margin-left:0.6em;
    }
    .row_three_left{
        float:left;
        width:33%;
    }
    .row_three_right{
        float:left;
        width:66%;
    }    
    .row_three_label{
        text-shadow:blue 0 0 1em;
        color:white;
        font-size:1.5em; 
        float:left;      
    }
    .row_three_content{
        float:left;
        font-align:left;
        font-size:1.5em; 
        margin-left:0.6em;    
    }
    #organization{
      float:left;
      width:50%;  
    }
    #organization_label{
      text-shadow:blue 0 0 1em;
      color:white;
      font-size:1.5em; 
      float:left;  
    }
    #study_id{
      float:left;
      width:50%;
    }
    #tissue_label{
      text-shadow:blue 0 0 1em;
      color:white;
      font-size:1.5em; 
      float:left;  
    }
    #study_id_label{
      text-shadow:blue 0 0 1em;
      color:white;
      font-size:1.5em; 
      float:left;  
    }
    #bigwig_label{
      text-shadow:blue 0 0 1em;
      color:white;
      font-size:1.5em; 
      float:left;  
    }
    .search_button{
        height:2em;
        margin:1.5em 0 0 0;
        float:left;
    } 
    button:hover{
        background-color:#EA493F
    }    
</style>
<script>
    export default{
        methods:{
            change_detail_button:function(){
                this.$emit(
                    "listen_detail_button",
                    '1'
                )                
            }         
        },
        props:['all_data']
    }
</script>



